.product {
  // Grid
  @media (min-width: $xl) {
    display: flex;
    gap: 30px;
  }

  &-bg-block {
    padding: 20px 15px;
    background-color: $white;

    @media (min-width: $md) {
      padding-right: 20px;
      padding-left: 20px;
    }
  }

  // Back btn
  &-back {
    @media (max-width: $max-md) {
      top: 15px;
    }

    @media (min-width: $md) and (max-width: $max-xl) {
      top: 20px;
    }

    @media (max-width: $max-xl) {
      position: absolute;
      z-index: 2;
      display: flex;
    }

    @media (min-width: $xl) {
      margin-bottom: 15px;
    }

    @media (min-width: $xxl) {
      margin-bottom: 30px;
    }

    &__link {
      @media (min-width: $xl) {
        display: flex;
        align-items: center;
        gap: 5px;
        font-size: 16px;
        font-family: $font-title;
      }
    }

    &__icon {
      display: block;

      @media (max-width: $max-md) {
        width: 40px;
        height: 40px;
      }

      @media (max-width: $max-xl) {
        padding: 10px;
        color: $secondary-clr;
        border: 1px solid $secondary-clr;
      }

      @media (min-width: $md) and (max-width: $max-xl) {
        width: 50px;
        height: 50px;
      }

      @media (min-width: $xl) {
        width: 24px;
        height: 24px;
      }
    }

    &__title {
      @media (max-width: $max-xl) {
        display: none;
      }
    }
  }

  &__left {
    @media (min-width: $xl) {
      width: 66%;
      flex: 1 1 875px;
    }

    @media (min-width: $xxl) {
      flex-basis: 1170px;
    }
  }

  &__right {
    @media (min-width: $xl) {
      width: 34%;
      flex: 1 1 415px;
    }

    @media (min-width: $xxl) {
      flex-basis: 520px;
    }
  }

  // Left block images
  &__images {
    @media (min-width: $xl) {
      display: flex;
      gap: 10px;
    }

    @media (min-width: $xxl) {
      gap: 30px;
    }
  }

  // Right block
  &__info {
    position: sticky;
    top: 90px;

    &-block {
      margin-bottom: 2px;
    }

    .accordion__title {
      font-family: $font-title;
      font-size: 10px;

      @media (min-width: $md) {
        font-size: 12px;
      }

      @media (min-width: $xl) {
        font-size: 16px;
      }
    }
  }

  &__title {
    @media (min-width: $xl) {
      font-size: 36px;
    }
  }

  &__price {
    display: block;
    margin-top: 10px;
  }

  &__size {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;

    @media (min-width: $xxl) {
      gap: 4px;
    }
  }

  &__sku {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-family: $font-title;
    font-size: 10px;

    @media (min-width: $md) {
      font-size: 12px;
    }

    @media (min-width: $xl) {
      font-size: 16px;
    }

    &-value {
      color: $gray;
    }
  }

  // Product additional block
  &__additional {
    margin-top: 20px;

    @media (min-width: $md) {
      margin-top: 30px;
    }

    &--desk {
      @media (max-width: $max-xl) {
        display: none;
      }
    }

    &--mob {
      @media (min-width: $xl) {
        display: none;
      }
    }
  }

  // Product banner
  &-banner {
    @media (max-width: $max-md) {
      display: none;
    }

    @media (min-width: $md) {
      margin-bottom: 30px;
      padding: 30px;
      background-color: $white;
    }

    @media (min-width: $xl) {
      margin-bottom: 60px;
    }

    &__img {
      margin: 0 auto 15px;
      width: 95px;
      color: $secondary-clr;

      @media (min-width: $xl) {
        margin-bottom: 25px;
      }
    }

    &__desc {
      margin: 0 auto;
      padding-bottom: 30px;
      max-width: 510px;
      text-align: center;

      @media (min-width: $xl) {
        max-width: 650px;
      }

      @media (min-width: $xxl) {
        max-width: 780px;
      }
    }
  }

  // Product related
  &-related__title {
    margin-bottom: 20px;

    @media (min-width: $md) {
      margin-bottom: 30px;
    }
  }
}

// Product main slider
// top
.gallery-top {
  @media (max-width: $max-md) {
    margin: 0 -15px 15px;
    padding-bottom: 23px;
  }

  @media (min-width: $md) and (max-width: $max-xl) {
    margin: 0 -20px 30px;
    padding-bottom: 45px;
  }

  @media (min-width: $xl) {
    flex: 0 0 565px;
  }

  @media (min-width: $xxl) {
    flex-basis: 860px;
  }

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 325px;
    background-color: $white;

    @media (min-width: $md) {
      height: 640px;
    }

    @media (min-width: $xl) {
      height: 470px;
    }

    @media (min-width: $xxl) {
      height: 730px;
    }

    img {
      width: 250px;
      height: 250px;

      @media (min-width: $md) {
        width: 420px;
        height: 420px;
      }

      @media (min-width: $xl) {
        width: 310px;
        height: 310px;
      }

      @media (min-width: $xxl) {
        width: 485px;
        height: 485px;
      }
    }
  }

  .swiper-pagination {
    bottom: -5px;

    @media (min-width: $xl) {
      display: none;
    }

    &-bullet {
      margin: 0 5px!important;
      background-image: url("../images/pagination-dot-empty.png");
      background-color: transparent;
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      opacity: 1;

      @media (min-width: $md) {
        width: 15px;
        height: 15px;
      }

      &-active {
        background-image: url("../images/pagination-dot-filled.png");
      }
    }
  }
}

// thumbs
.gallery-thumbs {
  @media (max-width: $max-xl) {
    display: none;
  }

  @media (min-width: $xl) {
    margin: 0;
    flex: 0 0 80px;
  }

  @media (min-width: $xxl) {
    flex-basis: 100px;
  }

  .swiper-slide {
    @media (min-width: $xl) {
      height: 80px;
    }

    @media (min-width: $xxl) {
      height: 100px;
    }

    &-thumb-active .gallery-thumbs__img {
      @media (min-width: $xl) {
        border-color: $main-clr;
      }
    }
  }

  &__img {
    @media (min-width: $xl) {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      border: 1px solid transparent;
      background-color: $white;
    }

    img {

      @media (min-width: $xl) {
        width: 65px;
        height: 65px;
      }
    }
  }
}
