.categories {
  // Heading
  &-heading {
    margin-bottom: 20px;

    @media (min-width: $md) {
      margin-bottom: 30px;
    }

    @media (min-width: $xxl) {
      margin-bottom: 50px;
    }

    &-tip {
      display: inline-block;
      margin-bottom: 10px;

      @media (min-width: $md) {
        margin-bottom: 15px;
      }
    }
  }

  // Section
  &-section {
    margin-bottom: 20px;
    padding: 15px 0;
    background-color: $white;

    @media (min-width: $md) {
      padding-top: 20px;
      padding-bottom: 20px;
    }

    @media (min-width: $xl) {
      margin-bottom: 30px;
      padding-top: 30px;
      padding-bottom: 30px;
    }

    @media (min-width: $xxl) {
      margin-bottom: 40px;
    }
  }

  // Slider
  &-slider {
    @media (max-width: $max-md) {
      padding-bottom: 20px;
    }

    &__img {
      margin-bottom: 5px;
      height: 110px;

      @media (min-width: $md) {
        height: 235px;
      }

      @media (min-width: $xl) {
        margin-bottom: 20px;
        height: 400px;
      }

      @media (min-width: $xxl) {
        height: 500px;
      }
    }

    &__name {
      @media (max-width: $max-md) {
        font-size: 12px;
      }

      @media (min-width: $md) and (max-width: $max-xl) {
        font-size: 14px;
      }
    }

    &__link {
      display: inline-block;
      color: $txt-clr;
    }

    .swiper-pagination {
      bottom: -5px;

      @media (min-width: $xl) {
        display: none;
      }

      &-bullet {
        margin: 0 5px!important;
        background-image: url("../images/pagination-dot-empty.png");
        background-color: transparent;
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
        opacity: 1;

        @media (min-width: $md) {
          width: 15px;
          height: 15px;
        }

        &-active {
          background-image: url("../images/pagination-dot-filled.png");
        }
      }
    }
  }
}
