.search {
  &-page {
    h1 {
      margin-bottom: 20px;

      @media (min-width: $xl) {
        margin-bottom: 50px;
      }
    }
  }

  &-result {
    background-color: $white;
    padding: 15px;

    @media (min-width: $sm) {
      display: flex;
      gap: 30px;
      flex-wrap: wrap;
      align-items: center;
    }

    @media (min-width: $md) {
      padding: 30px;
    }

    @media (min-width: $lg) {
      padding: 50px;
    }

    .form__group {
      @media (min-width: $sm) {
        margin-bottom: 0!important;
      }

      &--search-field {
        @media (min-width: $sm) {
          width: 100%;
        }

        @media (min-width: $lg) {
          width: auto;
          flex-grow: 1;
        }
      }

      &--search-select {
        @media (min-width: $sm) {
          flex-grow: 1;
        }

        @media (min-width: $lg) {
          min-width: 200px;
        }
      }

      &--search-submit {
        @media (min-width: $sm) {
          width: 180px;
        }
      }
    }
  }
}
