.payment {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 5px;
  padding: 15px;
  min-height: 360px;
  background-color: $white;

  @media (min-width: $md) {
    min-height: 660px;
  }

  @media (min-width: $xl) {
    margin: 0 auto;
    max-width: 1280px;
    min-height: 540px;
  }

  h1 {
    margin-bottom: 25px;

    @media (min-width: $md) {
      margin-bottom: 55px;
      width: 100%;
      max-width: 480px;
    }
  }

  .btn {
    max-width: 480px;
  }
}
